import React, { useState } from "react";
import "./reader.css";

const get_json = async () =>
  await fetch("fic_html/" + (window.sessionStorage.getItem("storyPath") ?? 'gbl_chunked') + "/sitemap.json").then(resp => resp.json());

const raw_json = get_json();

let chapter_paths_list = [];
let chapter_titles_list = [];

const get_chaps = async () => await raw_json.then(
  resp => {
    const chapter_paths = [];
    const chapter_titles = [];
    Object.entries(resp)[0][1].map(chap => {
      chapter_paths.push(chap.section.path);
      chapter_titles.push(chap.section.title);
      return 1;
    });
    chapter_paths_list = chapter_paths_list.concat(chapter_paths);
    chapter_titles_list = chapter_titles_list.concat(chapter_titles);
  }
)

const updateSessionStorage = (story) => { window.sessionStorage.setItem("storyPath", story) }

await get_chaps();

let currentChapter = 0;
let currentFontSize = 16; // 16 is the default font size

function Reader({ story }) {
  updateSessionStorage(story);

  const [isNotFirstChapter, setPreviousButtonVis] = useState(false);
  const [isNotLastChapter, setNextButtonVis] = useState(true);

  function change_chapter(jumping, new_chapter) {
    console.log(jumping)
    // for some reason "value" is treated as a string, so cast it to number
    currentChapter = jumping ? Number(document.getElementById("chapter-selection").value) : new_chapter
    console.log(currentChapter, typeof currentChapter)
    const iframe = document.getElementById("reader-iframe");
    iframe.src = "fic_html/" + story + "/" + chapter_paths_list[currentChapter];

    setNextButtonVis(currentChapter < (chapter_paths_list.length - 1));
    setPreviousButtonVis(currentChapter !== 0);
    iframe.onload = () => {
      // Ensure the content is fully loaded before modifying styles
      const iframeDocument = iframe.contentDocument || iframe.contentWindow.document;

      // Add a style tag to set the font-size property inside the iframe
      const styleTag = iframeDocument.createElement('style');
      styleTag.innerHTML = `body { font-size: ${currentFontSize}px; }`;
      iframeDocument.head.appendChild(styleTag);
    };
  }

  function changeFontSize(change_by) {
    currentFontSize += change_by
    const iframe = document.getElementById("reader-iframe")
    const innerDoc = iframe.contentDocument
    innerDoc.activeElement.style["font-size"] = currentFontSize + "px"
  }

  return (
    <div>
      {/* only show chapter dropdown if more thn 1 chapter */}
      {chapter_titles_list.length > 1 && <p>
        Jump to Chapter...<br />
        <select onInput={() => (change_chapter(true, 0))} id="chapter-selection">
          {[...Array(chapter_paths_list.length).keys()].map(num =>
            <option key={num} value={num}>{num + 1}. {chapter_titles_list[num]}</option>)}
        </select>
      </p>}
      <p>
        <button onClick={() => changeFontSize(-2)}>Dec. Font Size</button>
        <button onClick={() => changeFontSize(2)}>Inc. Font Size</button>
      </p>
      <div id="reader">
        <iframe id="reader-iframe" title="reader-iframe" src={"fic_html/" + story + "/" + chapter_paths_list[0]}></iframe>
      </div>
      <p>
        {isNotFirstChapter && <button onClick={() => { change_chapter(false, currentChapter - 1) }}>Previous Chapter</button>}
        {isNotLastChapter && <button onClick={() => { change_chapter(false, currentChapter + 1) }}>Next Chapter</button>}
      </p>
    </div>
  )
}

export default Reader;